
@import '../static/normal.css';
[v-cloak]{
   display: none;
}
*{
   margin: 0;
   padding: 0;
   -webkit-tap-highlight-color: transparent;
   -webkit-appearance: none;
}
body{
   font-size: .35rem;
   margin: 0 auto;
   -webkit-user-select: none;
      -moz-user-select: none;
       -ms-user-select: none;
           user-select: none;
}
ul li{
   list-style: none;
}
img{
   cursor: pointer;
}
.Inight{
   background:#444 !important;
}
#app {
   font-family: 'Avenir', Helvetica, Arial, sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   color: #2c3e50;
   position: relative;
   -webkit-transition: .3s;
   transition: .3s;
   width: 100%;
   background: #fafafa;
}
#header{
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: space-between;
   -webkit-box-sizing: border-box;
           box-sizing: border-box;
   padding:.15rem .2rem;
   position: fixed;
   top: 0;
   width: 100%;
   background: inherit;
}
.marL{
   margin-left: 40%;
}
.content_c p{
   text-indent: 2em;
   line-height: .5rem;
   margin: .4rem 0 .2rem;
   /*font-weight: 600;*/
}
.hideSetting {
   bottom:0 !important;
}
div.showSave {
   opacity: 1 !important;
   top: 0 !important;
   left: 0 !important;
   -webkit-transition:opacity .3s,top .3s, left 0s !important;
   transition:opacity .3s,top .3s, left 0s !important;
}
.SC{
   position: fixed;
   top: 50%;
   left:50%;
   -webkit-transform: translate(-50%,-50%);
           transform: translate(-50%,-50%);
   background: rgba(0,0,0,.8);
   border-radius: 8px;
   color: #fff;
   padding: .3rem .6rem;
}
.sc-leave-active{
   -webkit-transition: .1s .2s;
   transition: .1s .2s;
}
.sc-enter-active{
   -webkit-transition: .1s;
   transition: .1s;
}
.sc-enter,.sc-leave-to{
   opacity: 0;
}



